import React, { useEffect } from "react";
import { Box, FormLabel, Button, TextareaAutosize, RadioGroup, FormControlLabel, Radio, Checkbox, FormControl, FormGroup, TextField } from "@mui/material";
import { useFormContext, Controller, useFieldArray } from "react-hook-form";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFnsV3/AdapterDateFnsV3";
import { ru } from "date-fns/locale/ru";

interface FormData {
  disease: string //      -  /     
  diseasesItems: {
    name: string; //  /
    dateStart: Date | null; //    /
    status: string; //   /
    isTreatment: string; //   -  /
    treatmentDescription: string; //   ,  
  }[]
}

export const Step6: React.FC = () => {
  const { control, watch, unregister } = useFormContext<FormData>();

  const disease = watch("disease");
  const { fields, append, remove } = useFieldArray({
    control,
    name: "diseasesItems",
  });

  useEffect(() => {
    if (disease !== "") {
      unregister("diseasesItems");
    }
  }, [disease, unregister]);

  useEffect(() => {
    if (fields.length === 0) {
      append({ name: "", dateStart: null, status: "", isTreatment: "", treatmentDescription: ""}); //    
    }
  }, []);

  return (
    <Box>
      <h1 className="text-3xl font-thin">    -  /?</h1>



      <Controller
        name={`disease`}
        control={control}
        render={({ field, fieldState }) => (
          <div className="">
            <FormLabel error={!!fieldState.error} id="isContinue">     -  /     ?</FormLabel>
            <RadioGroup name="isContinue" className="">
              <FormControlLabel {...field} control={<Radio checked={disease === ""} style={{ padding: "0 8px 0 8px" }} />} label="" value="" />
              <FormControlLabel {...field} control={<Radio checked={disease === ",      /"} style={{ padding: "0 8px 0 8px" }} />} label=",      /" value=",      /" />
              <FormControlLabel {...field} control={<Radio checked={disease === ",      "} style={{ padding: "0 8px 0 8px" }} />} label=",      " value=",      " />
            </RadioGroup>
            {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
          </div>
        )}
      />

      {disease === "" && <Controller
        name={`diseasesItems`}
        control={control}
        render={({ field, fieldState }) => (
          <div className="mt-4">
            {fields.map((item, index) => (
              <Box key={item.id} className="mt-4 border p-2 rounded grid grid-cols-2 gap-4">
                <Controller
                  name={`diseasesItems.${index}.name`}
                  control={control}
                  render={({ field, fieldState }) => (
                    <TextField
                      {...field}
                      type="string"
                      className=" w-full"
                      label=" /"
                      error={!!fieldState.error}
                      helperText={fieldState.error?.message}
                    />
                  )}
                />

                <Controller
                  name={`diseasesItems.${index}.dateStart`}
                  control={control}
                  render={({ field, fieldState }) => (
                    <>
                      <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={ru}>
                        <DatePicker
                          {...field}
                          className=" w-full"
                          label="   /"
                          onChange={(date) => field.onChange(date)} //    react-hook-form
                        />
                      </LocalizationProvider>
                    </>
                  )}
                />

                <Controller
                  name={`diseasesItems.${index}.status`}
                  control={control}
                  // rules={{ required: " ,       ( )" }}
                  render={({ field, fieldState }) => (
                    <>
                      <TextareaAutosize
                        {...field}
                        className=" w-full p-4 border rounded"
                        aria-label="minimum height"
                        minRows={3}
                        placeholder="  /"
                      />
                    </>
                  )}
                />

                <Controller
                  name={`diseasesItems.${index}.isTreatment`}
                  control={control}
                  render={({ field, fieldState }) => (
                    <div className="">
                      <FormLabel error={!!fieldState.error} id="isContinue">  -  /?</FormLabel>
                      <RadioGroup name="isContinue" className="">
                        <FormControlLabel {...field} control={<Radio checked={field.value === ""} style={{ padding: "0 8px 0 8px" }} />} label="" value="" />
                        <FormControlLabel {...field} control={<Radio checked={field.value === ""} style={{ padding: "0 8px 0 8px" }} />} label="" value="" />
                      </RadioGroup>
                      {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
                    </div>
                  )}
                />

                <Controller
                  name={`diseasesItems.${index}.treatmentDescription`}
                  control={control}
                  // rules={{ required: " ,       ( )" }}
                  render={({ field, fieldState }) => (
                    <>
                      <TextareaAutosize
                        {...field}
                        className=" w-full p-4 border rounded"
                        aria-label="minimum height"
                        minRows={3}
                        placeholder="  ,  "
                      />
                    </>
                  )}
                />

                {index !== 0 && ( //      
                  <div className="">
                    <Button
                      variant="outlined"
                      color="error"
                      onClick={() => remove(index)}
                    >
                      
                    </Button>
                  </div>
                )}


              </Box>
            ))}
          </div>
        )}
      />}
      {disease === "" && <Button
        variant="contained"
        color="primary"
        onClick={() => append({ name: "", dateStart: null, status: "", isTreatment: "", treatmentDescription: ""})}
        style={{ marginTop: "20px" }}
      >
         /
      </Button>}
    </Box>
  );
};