import { Box, Button, FormControlLabel, FormLabel, MenuItem, Radio, RadioGroup, TextField } from "@mui/material";
import { Controller, useFieldArray, useFormContext } from "react-hook-form";
import { TextareaAutosize } from '@mui/base/TextareaAutosize';
import Select from "react-select"

import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
// import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { ru } from "date-fns/locale";
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3'
import { styled, css } from '@mui/system';
import { Modal as BaseModal } from '@mui/base/Modal';
import { drugNames } from "../../drugNames";
import { useEffect } from "react";

interface FormData {
  drugItems: {
    drugName: {}; //  
    drugNumber: string; //  
    purposeDrug: string; //  ,       ( )
    periodicity: string; //      
    dose: string; // 
    customPeriodicity: string; // ,     
    startDate: Date | null; //     ?
    isContinuePurpose: string; //       ?
    endDate: Date | null; //     ?
  }[]
}

export const Step3: React.FC = () => {
  const { control, watch, unregister } = useFormContext<FormData>();

  const { fields, append, remove } = useFieldArray({
    control,
    name: "drugItems",
  });

  const values = watch("drugItems");

  //     ,   
  useEffect(() => {
    if (fields.length === 0) {
      append({ drugName: "", drugNumber: "", purposeDrug: "", periodicity: "", dose: "", customPeriodicity: "", startDate: null, isContinuePurpose: "", endDate: null });
    }
  }, []);

  return (
    <Box>
      <h1 className="text-3xl font-thin">     ?</h1>
      <p className="opacity-50 ">    .          .</p>

      {fields.map((item, index) => (<Box key={index}>
        <div className="flex flex-col gap-3 p-4 border rounded mt-4">
          <Controller
            name={`drugItems.${index}.drugName`}
            control={control}
            rules={{ required: " " }}
            render={({ field, fieldState }) => (
              <div className="">
                <FormLabel error={!!fieldState.error} id="country">   ,      *</FormLabel>
                <Select
                  {...field}
                  styles={{
                    // Fixes the overlapping problem of the component
                    input: provided => ({ ...provided, paddingTop: 10, paddingBottom: 10 }),
                    menu: provided => ({ ...provided, zIndex: 9999 }),
                  }}
                  placeholder=" "
                  options={drugNames.map((item) => ({ value: item, label: item }))}
                />
              </div>
            )}
          />

          <Controller
            name={`drugItems.${index}.drugNumber`}
            control={control}
            render={({ field, fieldState }) => (
              <TextField
                {...field}
                type="string"
                className="w-full"
                label="  "
                error={!!fieldState.error}
                helperText={fieldState.error?.message}
              />
            )}
          />


          <div>
            <Controller
              name={`drugItems.${index}.purposeDrug`}
              control={control}
              render={({ field, fieldState }) => (
                <TextareaAutosize
                  {...field}
                  className="w-full p-4 border rounded"
                  aria-label="minimum height"
                  minRows={3}
                  placeholder=" ,       ( )"
                />
              )}
            />
          </div>

          <Controller
            name={`drugItems.${index}.periodicity`}
            control={control}
            render={({ field, fieldState }) => (
              <div className="flex flex-col">
                <FormLabel error={!!fieldState.error} id="periodicity">     ?</FormLabel>
                <RadioGroup name="periodicity">
                  <FormControlLabel {...field} value="" control={<Radio style={{ padding: 0, paddingLeft: "8px", paddingRight: "8px" }} checked={field.value === ""} />} label="" />
                  <FormControlLabel {...field} value="" control={<Radio style={{ padding: 0, paddingLeft: "8px", paddingRight: "8px" }} checked={field.value === ""} />} label="" />
                  <FormControlLabel {...field} value="  " control={<Radio style={{ padding: 0, paddingLeft: "8px", paddingRight: "8px" }} checked={field.value === "  "} />} label="  " />
                  <FormControlLabel {...field} value="  3 " control={<Radio style={{ padding: 0, paddingLeft: "8px", paddingRight: "8px" }} checked={field.value === "  3 "} />} label="  3 " />
                  <FormControlLabel {...field} value=" (, )" control={<Radio style={{ padding: 0, paddingLeft: "8px", paddingRight: "8px" }} checked={field.value === " (, )"} />} label=" (, )" />
                </RadioGroup>
                {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
              </div>
            )}
          />

          {values[index].periodicity === " (, )" && <Controller
            name={`drugItems.${index}.customPeriodicity`}
            control={control}
            render={({ field, fieldState }) => (
              <TextField
                {...field}
                type="string"
                className=""
                fullWidth
                label=",     "
                error={!!fieldState.error}
                helperText={fieldState.error?.message}
              />
            )}
          />}

          <Controller
            name={`drugItems.${index}.dose`}
            control={control}
            render={({ field, fieldState }) => (
              <TextField
                {...field}
                type="string"
                className=""
                fullWidth
                label=" (,     )"
                error={!!fieldState.error}
                helperText={fieldState.error?.message}
              />
            )}
          />


          <Controller
            name={`drugItems.${index}.startDate`}
            control={control}
            render={({ field, fieldState }) => (
              <>
                <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={ru}>
                  <DatePicker
                    {...field}
                    className="w-full"
                    label="    ?"
                    onChange={(date) => field.onChange(date)} //    react-hook-form
                  />
                </LocalizationProvider>
              </>
            )}
          />

          <Controller
            name={`drugItems.${index}.isContinuePurpose`}
            control={control}
            render={({ field, fieldState }) => (
              <div className="">
                <FormLabel error={!!fieldState.error} id="periodicity">      ?</FormLabel>
                <RadioGroup name="periodicity">
                  <FormControlLabel {...field} value="" control={<Radio style={{ padding: 0, paddingLeft: "8px", paddingRight: "8px" }} checked={field.value === ""} />} label="" />
                  <FormControlLabel {...field} value="" control={<Radio style={{ padding: 0, paddingLeft: "8px", paddingRight: "8px" }} checked={field.value === ""} />} label="" />
                </RadioGroup>
                {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
              </div>
            )}
          />

          {values[index].isContinuePurpose === "" && <Controller
            name={`drugItems.${index}.endDate`}
            control={control}
            render={({ field, fieldState }) => (
              <>
                <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={ru}>
                  <DatePicker
                    {...field}
                    className="w-full"
                    label="    ?"
                    onChange={(date) => field.onChange(date)} //    react-hook-form
                  />
                </LocalizationProvider>
              </>
            )}
          />}

          {index !== 0 && ( //      
            <div className="">
              <Button
                variant="outlined"
                color="error"
                onClick={() => remove(index)}
                style={{ marginTop: "10px" }}
              >
                
              </Button>
            </div>
          )}
        </div>


      </Box>))}


      <Button
        variant="contained"
        color="primary"
        onClick={() => append({ drugName: "", drugNumber: "", purposeDrug: "", periodicity: "", dose: "", customPeriodicity: "", startDate: null, isContinuePurpose: "", endDate: null })}
        style={{ marginTop: "20px" }}
      >
         
      </Button>
    </Box>
  );
};