import { Box, FormControlLabel, FormLabel, MenuItem, Radio, RadioGroup, TextField } from "@mui/material";
import { Controller, useFormContext } from "react-hook-form";
import { TextareaAutosize } from '@mui/base/TextareaAutosize';

import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
// import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { ru } from "date-fns/locale";
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3'
import { useEffect } from "react";


interface FormData {
  gender: string; // 
  isPregnant: string; // 
  dateOfBirth: Date | null; //  
  weekOfBirth: number | null; //  
  description: string; //         
  age: number | string; // 
}

export const Step2: React.FC = () => {
  const { control, watch, unregister } = useFormContext<FormData>();

  const gender = watch("gender");
  const isPregnant = watch("isPregnant");

  useEffect(() => {
    if (gender !== "") {
      unregister("isPregnant");
      unregister("dateOfBirth");
      unregister("weekOfBirth");
      unregister("description");
      unregister("age");
    }
  }, [gender, unregister]);

  useEffect(() => {
    if (isPregnant !== "") {
      unregister("dateOfBirth");
      unregister("weekOfBirth");
      unregister("description");
    }
  }, [isPregnant, unregister]);



  return (
    <Box>
      <h1 className="text-3xl font-thin">    ?</h1>
      <Controller
        name="gender"
        control={control}
        rules={{ required: " " }}
        render={({ field, fieldState }) => (
          <>
            <FormLabel error={!!fieldState.error} id="gender">  *</FormLabel>
            <RadioGroup name="gender">
              <FormControlLabel {...field} value="" control={<Radio checked={gender === ""} />} label="" />
              <FormControlLabel {...field} value="" control={<Radio checked={gender === ""} />} label="" />
              <FormControlLabel {...field} value=" " control={<Radio checked={gender === " "} />} label=" " />
            </RadioGroup>
            {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
          </>
        )}
      />

      {gender === "" && <Controller
        name="isPregnant"
        control={control}
        rules={{ required: " " }}
        render={({ field, fieldState }) => (
          <>
            <FormLabel error={!!fieldState.error} id="isPregnant">        ?</FormLabel>
            <RadioGroup name="isPregnant">
              <FormControlLabel {...field} value="" control={<Radio checked={isPregnant === ""} />} label="" />
              <FormControlLabel {...field} value="" control={<Radio checked={isPregnant === ""} />} label="" />
            </RadioGroup>
            {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
          </>
        )}
      />}

      {isPregnant === "" && gender === "" && (
        <>
          <Controller
            name="dateOfBirth"
            control={control}
            render={({ field, fieldState }) => (
              <>
                {/* <FormLabel error={!!fieldState.error} id="dateOfBirth">  (  )</FormLabel> */}
                <div className="flex mt-4 max-md:flex-col gap-2 items-center">
                  <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={ru}>
                    <DatePicker
                      {...field}
                      className="md:max-w-[400px] w-full"
                      label="  (  )"
                      onChange={(date) => field.onChange(date)} //    react-hook-form
                    />
                  </LocalizationProvider>
                  <span></span>
                  <Controller
                    name="weekOfBirth"
                    control={control}
                    render={({ field, fieldState }) => (
                      <TextField
                        {...field}
                        type="number"
                        className="md:max-w-[400px] w-full"
                        label=" / "
                        fullWidth
                        error={!!fieldState.error}
                        helperText={fieldState.error?.message}
                      />
                    )}
                  />
                </div>
              </>
            )}
          />
          <Controller
            name="description"
            control={control}
            render={({ field, fieldState }) => (
              <TextareaAutosize
                {...field}
                className="max-w-[400px] w-full mt-4 p-4 border rounded"
                aria-label="minimum height"
                minRows={3}
                placeholder=",          ."
              />
            )}
          />

        </>)
      }
      <Controller
        name="age"
        control={control}
        rules={{
          validate: (value) => {
            if (value === undefined || value === "" || value === null) {
              return true; //  ,  
            }
            if (Number(value) < 1) {
              return "    0";
            }
            if (Number(value) > 120) {
              return "    120";
            }
            return true; //   
          },
        }}
        render={({ field, fieldState }) => (
          <TextField
            {...field}
            type="number"
            margin="normal"
            label="  ( )"
            fullWidth
            error={!!fieldState.error}
            helperText={fieldState.error?.message}
          />
        )}
      />
    </Box>
  );
};