import { Box, FormControl, FormControlLabel, FormLabel, MenuItem, Radio, RadioGroup, TextField } from "@mui/material";
import { Controller, useFormContext } from "react-hook-form";
import Select from "react-select"
import { countries } from "../../countries";
import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'
import { useEffect } from "react";

interface FormData {
  isCommunication: string; //      ,       ,     ,   ?
  contactMethod: "email" | "phone"; //  
  phone: string; // 
  email: string; // Email
  isPatient: string; //  - 
  agent: string; //   
  country: {}; // 
  isLundbeckDrugPurchase: string; //         ,    ?
  sideEffectCountry: {}; // ,     
}

export const Step1: React.FC = () => {
  const { control, watch, unregister, setValue } = useFormContext<FormData>();
  const isCommunication = watch("isCommunication");
  const isPatient = watch("isPatient");
  const contactMethod = watch("contactMethod");
  const isLundbeckDrugPurchase = watch("isLundbeckDrugPurchase");


  useEffect(() => {
    setValue("country", { label: "", value: "" })
  }, [])

  useEffect(() => {
    if (isCommunication === "") {
      unregister("contactMethod")
      unregister("phone")
      unregister("email")
    }
  }, [isCommunication])

  useEffect(() => {
    if (contactMethod === "phone")
      unregister("email")
    if (contactMethod === "email")
      unregister("phone")
  }, [contactMethod])

  useEffect(() => {
    if (isPatient === "") {
      unregister("agent");
    }
  }, [isPatient, unregister]);

  useEffect(() => {
    if (isCommunication === "") {
      unregister("contactMethod");
    }
  }, [isCommunication, unregister]);

  useEffect(() => {
    if (isLundbeckDrugPurchase === "") {
      unregister("sideEffectCountry");
    }
    if (isLundbeckDrugPurchase === "") {
      setValue("sideEffectCountry", { "value": "", "label": "" });
    }
  }, [isLundbeckDrugPurchase, unregister]);

  return (
    <Box>
      <div className="flex flex-col gap-4">
        <Controller
          name="isCommunication"
          control={control}
          rules={{ required: " " }}
          render={({ field, fieldState }) => (
            <>
              <FormLabel error={!!fieldState.error} id="isCommunication">     ,       ,     ,   ? *</FormLabel>
              <RadioGroup name="isCommunication">
                <FormControlLabel {...field} value="" control={<Radio checked={isCommunication === ""} />} label="" />
                <FormControlLabel {...field} value="" control={<Radio checked={isCommunication === ""} />} label="" />
              </RadioGroup>
              {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
            </>
          )}
        />
        {isCommunication === "" && <>
          <Controller
            name="contactMethod"
            control={control}
            rules={{ required: "  " }}
            render={({ field, fieldState }) => (
              <TextField
                {...field}
                select
                label="   "
                fullWidth
                margin="normal"
                error={!!fieldState.error}
              // helperText={fieldState.error?.message}
              >
                <MenuItem value="email">Email</MenuItem>
                <MenuItem value="phone"></MenuItem>
              </TextField>
            )}
          />
          {contactMethod === "email" && isCommunication === "" && <Controller
            name="email"
            control={control}
            rules={{
              required: contactMethod === "email" ? " email" : false,
              pattern: contactMethod === "email" ? {
                value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                message: "  email"
              } : undefined,
            }}
            render={({ field, fieldState }) => (
              <TextField
                {...field}
                fullWidth
                margin="normal"
                error={!!fieldState.error}
                label="Email"
                helperText={fieldState.error?.message}
              />
            )}
          />}
          {contactMethod === "phone" && isCommunication === "" && <Controller
            name="phone"
            control={control}
            rules={{ required: " " }}
            render={({ field, fieldState }) => (
              <>
                <PhoneInput
                  inputStyle={{ width: "100%", height: "60px" }}
                  {...field}
                />
                {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
              </>
            )}
          />}
        </>}


        <Controller
          name="isPatient"
          control={control}
          rules={{ required: " " }}
          render={({ field, fieldState }) => (
            <>
              <FormLabel error={!!fieldState.error} id="isPatient">  ,     ? *</FormLabel>
              <RadioGroup name="isPatient">
                <FormControlLabel {...field} value="" control={<Radio checked={isPatient === ""} />} label="" />
                <FormControlLabel {...field} value="" control={<Radio checked={isPatient === ""} />} label="" />
              </RadioGroup>
              {/* {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>} */}
            </>
          )}
        />

        {isPatient === "" && <>
          <Controller
            name="agent"
            control={control}
            rules={{ required: " " }}
            render={({ field, fieldState }) => (
              <>
                <FormLabel error={!!fieldState.error} id="agent">  </FormLabel>
                <RadioGroup name="agent">
                  <FormControlLabel {...field} value=" /,    " control={<Radio />} label=" /,    " />
                  <FormControlLabel {...field} value="    ,    " control={<Radio />} label="    ,    " />
                  <FormControlLabel {...field} value="  " control={<Radio />} label="  " />
                </RadioGroup>
                {/* {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>} */}
              </>
            )}
          />
        </>}

        <Controller
          name="country"
          control={control}
          rules={{ required: " " }}
          render={({ field, fieldState }) => (
            <>
              <FormLabel error={!!fieldState.error} id="country">     ? *</FormLabel>
              <Select
                {...field}
                defaultValue={{ "value": "", "label": "" }}
                options={countries.map((country) => ({ value: country, label: country }))}
              />
            </>
          )}
        />

        <Controller
          name="isLundbeckDrugPurchase"
          control={control}
          render={({ field, fieldState }) => (
            <>
              <FormLabel error={!!fieldState.error} id="isLundbeckDrugPurchase">        ,    ?</FormLabel>
              <RadioGroup name="">
                <FormControlLabel {...field} value="" control={<Radio checked={isLundbeckDrugPurchase === ""} />} label="" />
                <FormControlLabel {...field} value="" control={<Radio checked={isLundbeckDrugPurchase === ""} />} label="" />
              </RadioGroup>
              {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
            </>
          )}
        />

        {isLundbeckDrugPurchase === "" && <Controller
          name="sideEffectCountry"
          control={control}
          rules={{ required: " " }}
          render={({ field, fieldState }) => (
            <>
              <FormLabel error={!!fieldState.error} id="sideEffectCountry">     ? *</FormLabel>
              <Select
                {...field}
                defaultValue={{ "value": "", "label": "" }}
                options={countries.map((country) => ({ value: country, label: country }))}
              />
            </>
          )}
        />}
      </div>
    </Box>
  );
};
