export interface DataToSend {
  isCommunication: string;
  contactMethod: "email" | "phone";
  phone: string;
  email: string;
  isPatient: string;
  agent: string;
  country: {};
  isLundbeckDrugPurchase: string;
  sideEffectCountry: {};
  gender: string;
  isPregnant: string;
  dateOfBirth: Date | null;
  weekOfBirth: number | null;
  description: string;
  age: number | string;
  drugItems: {
    drugName: {};
    drugNumber: string;
    purposeDrug: string;
    periodicity: string;
    dose: string;
    customPeriodicity: string;
    startDate: Date | null;
    isContinuePurpose: string;
    endDate: Date | null;
  }[];
  sideEffects: {
    sideEffectDescription: string;
    sideEffectStartDate: Date | null;
    sideEffectStatus: string;
    sideEffectEndDate: Date | null;
  }[];
  consequences: Record<string, boolean>;
  ateMedicine: string;
  anyDrugs: {
    name: string;
    reason: string;
    dose: string;
    dateStart: Date | null;
    isContinue: string;
    additionalDrugs: string;
    dateEnd: Date | null;
  }[];
  disease: string;
  diseasesItems: {
    name: string;
    dateStart: Date | null;
    status: string;
    isTreatment: string;
    treatmentDescription: string;
  }[];
}

export default function generateEmailContent(data: DataToSend): string {
  const drugItemsContent = data.drugItems
    .map(
      (item, index) => `
     ${index + 1}:
      - : ${item.drugName ? JSON.stringify(item.drugName) : " "}
      - : ${item.drugNumber || " "}
      - : ${item.purposeDrug || " "}
      - : ${item.periodicity} (${item.customPeriodicity || " "})
      - : ${item.dose || " "}
      -  : ${item.startDate || " "}
      -   : ${item.isContinuePurpose || " "}
      -  : ${item.endDate || " "}`
    )
    .join("\n");

  const sideEffectsContent = data.sideEffects
    .map(
      (effect, index) => `
      ${index + 1}:
      - : ${effect.sideEffectDescription || " "}
      -  : ${effect.sideEffectStartDate || " "}
      - : ${effect.sideEffectStatus || " "}
      -  : ${effect.sideEffectEndDate || " "}`
    )
    .join("\n");

  const anyDrugsContent = data.anyDrugs
    .map(
      (drug, index) => `
      ${index + 1}:
      - : ${drug.name || " "}
      - : ${drug.reason || " "}
      - : ${drug.dose || " "}
      -  : ${drug.dateStart || " "}
      -   : ${drug.isContinue || " "}
      -  : ${drug.additionalDrugs || " "}
      -  : ${drug.dateEnd || " "}`
    )
    .join("\n");

  const diseasesContent = data.diseasesItems
    .map(
      (disease, index) => `
     ${index + 1}:
      - : ${disease.name || " "}
      -  : ${disease.dateStart || " "}
      - : ${disease.status || " "}
      -  : ${disease.isTreatment || " "}
      -  : ${disease.treatmentDescription || " "}`
    )
    .join("\n");

  return `
   :
    -   : ${data.isCommunication || " "}
    -  : ${data.contactMethod || " "}
    - : ${data.phone || " "}
    - Email: ${data.email || " "}
    -  : ${data.isPatient || " "}
    -   : ${data.agent || " "}
    - : ${JSON.stringify(data.country)}
    -       : ${data.isLundbeckDrugPurchase || " "}
    -    : ${data.sideEffectCountry ? JSON.stringify(data.sideEffectCountry) : " "}
    - : ${data.gender || " "}
    - : ${data.isPregnant || " "}
    -  : ${data.dateOfBirth || " "}
    -  : ${data.weekOfBirth || " "}
    - : ${data.age || " "}
    -  : ${data.description || " "}
    -   : ${data.ateMedicine || " "}
    -     : ${data.disease || " "}

  :
  ${drugItemsContent}

   :
  ${sideEffectsContent}

   :
  ${anyDrugsContent}

  :
  ${diseasesContent}
  `;
}