import React, { useEffect } from "react";
import { Box, FormLabel, Button, TextareaAutosize, RadioGroup, FormControlLabel, Radio, Checkbox, FormControl, FormGroup, TextField } from "@mui/material";
import { useFormContext, Controller, useFieldArray } from "react-hook-form";
import Select from "react-select";
import { drugNames } from "../../drugNames";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFnsV3/AdapterDateFnsV3";
import { ru } from "date-fns/locale/ru";

interface FormData {
  ateMedicine: string; //     
  anyDrugs: {
    name: string; //    ,   
    reason: string; //  ,       ( )
    dose: string; //    (    )
    dateStart: Date | null; //    
    isContinue: string; //       ?
    additionalDrugs: string; //     ,    ?
    dateEnd: Date | null; //     ?
  }[];
}

export const Step5: React.FC = () => {
  const { control, watch, unregister } = useFormContext<FormData>();

  const { fields, append, remove } = useFieldArray({
    control,
    name: "anyDrugs",
  });

  const ateMedicine = watch("ateMedicine");


  useEffect(() => {
    if (ateMedicine !== "") {
      unregister("anyDrugs");
    }
  }, [ateMedicine, unregister]);

  useEffect(() => {
    if (fields.length === 0) {
      append({ name: "", reason: "", dose: "", dateStart: null, isContinue: "", dateEnd: null, additionalDrugs: "" }); //    
    }
  }, []);


  return (
    <Box>
      <h1 className="text-3xl font-thin">     ( )?</h1>

      <Controller
        name={`ateMedicine`}
        control={control}
        render={({ field, fieldState }) => (
          <div className="mt-4">
            <FormLabel error={!!fieldState.error} id="periodicity">     ( )     ?</FormLabel>
            <RadioGroup name="periodicity" className="mt-2">
              <FormControlLabel {...field} control={<Radio checked={field.value === ""} style={{ padding: "0 8px 0 8px" }} />} label="" value="" />
              <FormControlLabel {...field} control={<Radio checked={field.value === ",     "} style={{ padding: "0 8px 0 8px" }} />} label=",     " value=",     " />
              <FormControlLabel {...field} control={<Radio checked={field.value === "     "} style={{ padding: "0 8px 0 8px" }} />} label="     " value="     " />
            </RadioGroup>
            {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
          </div>
        )}
      />

      {ateMedicine === "" && <Controller
        name={`anyDrugs`}
        control={control}
        render={({ field, fieldState }) => (
          <div className="mt-4">
            {fields.map((item, index) => (
              <Box key={item.id} className="mt-4 border p-2 rounded grid grid-cols-2 gap-4">
                <Controller
                  name={`anyDrugs.${index}.name`}
                  control={control}
                  // rules={{ required: "    ( ),        " }}
                  render={({ field, fieldState }) => (
                    <>
                      <TextareaAutosize
                        {...field}
                        className=" w-full p-4 border rounded"
                        aria-label="minimum height"
                        minRows={3}
                        placeholder="   ,        "
                      />
                    </>
                  )}
                />
                <Controller
                  name={`anyDrugs.${index}.reason`}
                  control={control}
                  // rules={{ required: " ,       ( )" }}
                  render={({ field, fieldState }) => (
                    <>
                      <TextareaAutosize
                        {...field}
                        className=" w-full p-4 border rounded"
                        aria-label="minimum height"
                        minRows={3}
                        placeholder=" ,       ( )"
                      />
                    </>
                  )}
                />
                <Controller
                  name={`anyDrugs.${index}.dose`}
                  control={control}
                  render={({ field, fieldState }) => (

                    <TextField
                      {...field}
                      type="string"
                      className=" w-full"
                      label="   (    )"
                      error={!!fieldState.error}
                      helperText={fieldState.error?.message}
                    />
                  )}
                />

                <Controller
                  name={`anyDrugs.${index}.dateStart`}
                  control={control}
                  render={({ field, fieldState }) => (
                    <>
                      <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={ru}>
                        <DatePicker
                          {...field}
                          className=" w-full"
                          label="   "
                          onChange={(date) => field.onChange(date)} //    react-hook-form
                        />
                      </LocalizationProvider>
                    </>
                  )}
                />

                <Controller
                  name={`anyDrugs.${index}.isContinue`}
                  control={control}
                  render={({ field, fieldState }) => (
                    <div className="">
                      <FormLabel error={!!fieldState.error} id="isContinue">      ?</FormLabel>
                      <RadioGroup name="isContinue" className="">
                        <FormControlLabel {...field} control={<Radio checked={field.value === ""} style={{ padding: "0 8px 0 8px" }} />} label="" value="" />
                        <FormControlLabel {...field} control={<Radio checked={field.value === ""} style={{ padding: "0 8px 0 8px" }} />} label="" value="" />
                      </RadioGroup>
                      {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
                    </div>
                  )}
                />
                <Controller
                  name={`anyDrugs.${index}.additionalDrugs`}
                  control={control}
                  render={({ field, fieldState }) => (
                    <div className="">
                      <FormLabel error={!!fieldState.error} id="additionalDrugs">    ,    ?</FormLabel>
                      <RadioGroup name="additionalDrugs" className="">
                        <FormControlLabel {...field} control={<Radio checked={field.value === ""} style={{ padding: "0 8px 0 8px" }} />} label="" value="" />
                        <FormControlLabel {...field} control={<Radio checked={field.value === ""} style={{ padding: "0 8px 0 8px" }} />} label="" value="" />
                      </RadioGroup>
                      {fieldState.error && <p className="text-[#d32f2f]">{fieldState.error.message}</p>}
                    </div>
                  )}
                />

                <Controller
                  name={`anyDrugs.${index}.dateEnd`}
                  control={control}
                  render={({ field, fieldState }) => (
                    <>
                      <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={ru}>
                        <DatePicker
                          {...field}
                          className=" w-full"
                          label="    ?"
                          onChange={(date) => field.onChange(date)} //    react-hook-form
                        />
                      </LocalizationProvider>
                    </>
                  )}
                />


                {index !== 0 && ( //      
                  <div className="">
                    <Button
                      variant="outlined"
                      color="error"
                      onClick={() => remove(index)}
                    >
                      
                    </Button>
                  </div>
                )}

              </Box>
            ))}
          </div>
        )}
      />}

      {ateMedicine === "" && <Button
        variant="contained"
        color="primary"
        onClick={() => append({ name: "", reason: "", dose: "", dateStart: null, isContinue: "", dateEnd: null, additionalDrugs: "" })}
        style={{ marginTop: "20px" }}
      >
          
      </Button>}
    </Box>
  );
};